<template>
  <div class="container">
    <div class="selector">
      <div class="input-item">
        <label>区域筛选</label>
        <div class="selecteds" v-for="(item,index) in selectedList" :key="index">
          <span>{{item}}</span>
          <div class="closeSelected">
            <img @click="event().toDeleteProvince(index)" style="cursor: pointer;" width="10px" src="@/assets/images/symbols-icons-light-close.png" alt="">
          </div>
        </div>
        <div class="icon" v-if="!showInput">
          <img width="100%" @click="showInput=true" src="@/assets/images/add.png" alt="">
        </div>
        <div class="input" v-if="showInput">
          <input v-model="province" type="text" placeholder="筛选省名称">
          <img @click="event().selectedProvince()" style="margin:0 5px;cursor: pointer;" width="25px" src="@/assets/images/symbols-icons-light-check.png" alt="">
          <img @click="showInput=false" style="cursor: pointer;" width="14px" src="@/assets/images/symbols-icons-light-close.png" alt="">
        </div>
      </div>
    </div>
    <div class="histogram-con">
      <div class="china"></div>
      <div class="who"></div>
      <div id="myBarChart" ref="barCharts" class="map"></div>
    </div>
  </div>
</template>

<script>
// import axios from "axios";
// var XLSX = require("xlsx");
// 引入基本模板
import { mapState } from "vuex";
import { GRID } from "@/jslib/areaAddress";
var echarts = require("echarts");
// import "echarts/lib/component/title";
// import "echarts/lib/component/legend";
import "echarts/lib/chart/heatmap";
import "echarts/lib/component/toolbox";
import "echarts/lib/component/tooltip";
// import areaData from "@/jslib/areaData";
export default {
  data() {
    return {
      showInput: false,
      whoMin: 0,
      whoMax: 10, //who最大的也就是中国最小的
      chinaMax: 35,
      province: "",
      selectedList: [],
      echartsOption: {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        // legend: {
        //   // type:'scroll',
        //   data: ["WHO标准", "中国国家标准"]
        // },
        dataZoom: [
          {
            type: "slider",
            zoomLock: true,
            handleSize: 0, //滑动条的 左右2个滑动条的大小
            handleColor: "#32ADFE", //h滑动图标的颜色
            backgroundColor: "#D5EEFF", //两边未选中的滑动条区域的颜色
            fillerColor: "#32ADFE",
            show: true,
            yAxisIndex: [0],
            start: 0, //数据窗口范围的起始百分比
            end: 2
          }
        ],
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "value",
          boundaryGap: [0, 0.01],
          min: 0,
          max: 200,
          splitNumber: 20,
          splitLine: {
            show: true,
            lineStyle: {
              color: "#fff"
            }
          },
          splitArea: {
            show: true,
            areaStyle: {
              color: ["#EBEBEB", "#EBEBEB"]
            }
          }
        },
        yAxis: {
          type: "category",
          data: []
        },
        itemStyle: {
          color: "#32ADFE ",
          barBorderRadius: [0, 10, 10, 0]
        },
        series: []
      },
      allCitiesArray: [],
      citiesArray: [],
      yearsAndDataArray: [],
      pollutantsArray: ["CO", "NO2", "O3", "PM10", "PM2d5", "SO2"],
      yearsArray: ["2013", "2014", "2015", "2016", "2017", "2018"],
      citiesAndPollutantsData: {}, // 污染物总数据
      hotCityData: {},
      limit: 400, // TODO: 调整城市数量限制
      pollutantsAndAllCities: {},
      pollutantProvinceCity: {},
      policiesArray: ["GT", "SN", "GL", "HBC", "QY"],
      citiesAndPoliciesData: {}, // 政策总数据
      policiesAndAllCities: {},
      policyProvinceCity: {},
      areaHotCityData: {}
    };
  },
  created() {
    if (this.isIE()) {
      this.echartsOption.toolbox = {
        show: true,
        showTitle: true,
        orient: "vertical",
        right: "20px",
        top: "center",
        feature: {
          saveAsImage: { show: true }
        }
      };
    }
  },
  mounted() {
    this.initData();

    this.drawMap();
    this.switchArea(0);
    if (this.chartData.tab === 1) {
      this.switchPollutant(0);
      // this.putChartOptions()
      this.refreshChart(this.chartData);
    }
    if (this.chartData.tab === 0) {
      // TODO: 视情况修改
      this.switchPolicy("GT");
      // this.putChartOptions()

      this.refreshPolicyChart(this.chartData);
    }
  },
  computed: {
    ...mapState([
      "PM2d5",
      "CO",
      "NO2",
      "PM10",
      "SO2",
      "O3",
      "cityData",
      "chartData",
      "importantCity",
      "hotCity"
    ]),
    returnChartDataObj() {
      let newChartDataObj = {};
      newChartDataObj.area = this.chartData.area;
      newChartDataObj.tab = this.chartData.tab;
      newChartDataObj.heatYear = this.chartData.heatYear;
      newChartDataObj.barchartYear = this.chartData.barchartYear;
      newChartDataObj.policy = this.chartData.policy;
      newChartDataObj.pollutant = this.chartData.pollutant;
      return newChartDataObj;
    }
  },
  watch: {
    returnChartDataObj(newValue, oldValue) {
      console.log(newValue, oldValue);
      if (newValue.area !== oldValue.area) {
        this.province = "";
        this.selectedList = [];
        this.showInput = false;
      }
      if (newValue.area > 2) {
        this.echartsOption.dataZoom[0].end = 50;
      } else if (newValue.area === 2) {
        this.echartsOption.dataZoom[0].end = 10;
      } else {
        this.echartsOption.dataZoom[0].end = 2;
      }
      if (newValue.tab === 1) {
        this.refreshChart(newValue, oldValue);
        this.echartsOption.xAxis.min = 0;
        this.echartsOption.xAxis.max = 200;
      }
      if (newValue.tab === 0) {
        this.echartsOption.dataZoom[0].end = 20;
        delete this.echartsOption.xAxis.min;
        delete this.echartsOption.xAxis.max;
        this.refreshPolicyChart(newValue, oldValue);
      }
    }
  },
  methods: {
    isIE() {
      if (!!window.ActiveXObject || "ActiveXObject" in window) {
        return true;
      } else {
        return false;
      }
    },
    drawMap() {
      this.myChart = echarts.init(document.getElementById("myBarChart"));
      this.myChart.setOption(this.echartsOption);
      let _this = this;
      setTimeout(function() {
        window.onresize = function() {
          _this.myChart.resize();
        };
      }, 200);

      // console.log("haha");
    },
    event() {
      return {
        selectedProvince: () => {
          if (this.province === "") {
            alert("筛选条件不能为空！");
            return;
          }
          if (this.selectedList.indexOf(this.province) !== -1) {
            alert("不能重复筛选");
            return;
          }
          // if (this.chartData.area === 1 && this.selectedList.length >= 1) {
          //   alert("城市只能单选");
          //   return;
          // }

          this.selectedList.push(this.province);

          if (this.chartData.area === 1) {
            // 城市
            this.citiesArray = this.selectedList;
          }
          this.echartsOption.dataZoom[0].end = 100;
          if (this.chartData.area === 0) {
            if (this.chartData.tab === 1) {
              // 省份
              let pollutant = this.findPollutant(this.chartData.pollutant);
              this.citiesArray = this.selectedList.reduce((prev, next) => {
                return [
                  ...prev,
                  ...this.pollutantProvinceCity[pollutant][next]
                ];
              }, []);
            }

            if (this.chartData.tab === 0) {
              this.citiesArray = this.selectedList.reduce((prev, next) => {
                return [
                  ...prev,
                  ...this.policyProvinceCity[this.chartData.policy][next]
                ];
              }, []);
            }
            // console.log(this.citiesArray);
          }
          if (this.chartData.tab === 1) {
            this.switchPollutant(this.chartData.pollutant);
          }

          if (this.chartData.tab === 0) {
            this.switchPolicy(this.chartData.policy);
          }

          let yearsArray = this.chartData.barchartYear.map(number => {
            switch (number) {
              case 0:
                return 2013;
                break;
              case 1:
                return 2014;
                break;
              case 2:
                return 2015;
                break;
              case 3:
                return 2016;
                break;
              case 4:
                return 2017;
                break;
              case 5:
                return 2018;
                break;

              default:
                break;
            }
          });
          // debugger
          this.filterYear(yearsArray);
          // }
          this.putChartOptions();

          // }
          // if (this.chartData.area === 0) {

          // }
        },
        toDeleteProvince: index => {
          this.selectedList.splice(index, 1);
          if (this.selectedList.length === 0) {
            // this.echartsOption.dataZoom[0].end = 2;
          }
          if (this.chartData.tab === 1) {
            this.refreshChart(this.chartData);
          }
          if (this.chartData.tab === 0) {
            this.refreshPolicyChart(this.chartData);
          }
        }
      };
    },
    returnChartNode() {
      return this.$refs.barCharts;
    },
    network() {
      return {};
    },
    handler() {
      return {
        //整理政策进展数据
        resetData: type => {
          let newCityData = {};
          let allCity = [];
          let keys = [];
          newCityData[type] = [];
          this.yearsArray.forEach(y => {
            keys.push(type + y);
          });
          console.log(keys);
          this.cityData.forEach((item, index) => {
            if (index < this.yearsArray.length) {
              newCityData[type].push({
                year: this.yearsArray[index],
                data: item[keys[index]] || 0
              });
            }
          });
          console.log(newCityData);
        }
      };
    },
    initData() {
      // 用来存放主要数据
      this.citiesAndPollutantsData = {};
      this.pollutantsArray.forEach(pollutant => {
        // 把 vuex 里的几个以 pollutant 为 key 的数据 forEach 一遍
        this[pollutant].forEach(item => {
          // 处理 pollutant -> province -> city 的数据

          if (!this.pollutantProvinceCity[pollutant]) {
            this.pollutantProvinceCity[pollutant] = {};
          }

          if (!this.pollutantProvinceCity[pollutant][item["省份"]]) {
            this.pollutantProvinceCity[pollutant][item["省份"]] = [];
          }

          this.pollutantProvinceCity[pollutant][item["省份"]].push(
            item["城市"]
          );

          // 处理主要数据
          if (!this.citiesAndPollutantsData[item["城市"]]) {
            this.citiesAndPollutantsData[item["城市"]] = {};
          }

          if (!this.citiesAndPollutantsData[item["城市"]][pollutant]) {
            this.citiesAndPollutantsData[item["城市"]][pollutant] = [];
          }
          // this.citiesAndPollutantsData['深圳']['PM2d5']

          this.yearsArray.forEach(year => {
            this.citiesAndPollutantsData[item["城市"]][pollutant].push({
              year,
              data: item[year] || 0
            });
          });
        });

        this.pollutantsAndAllCities[pollutant] = this[pollutant].map(
          i => i["城市"]
        );
      });

      // 数据处理完了
      // 不你没有
      // 开始处理 hot city
      this.hotCityData = {};
      this.hotCity.forEach(item => {
        if (!this.hotCityData[item["区域"]]) {
          this.hotCityData[item["区域"]] = [];
        }

        this.hotCityData[item["区域"]].push(item["城市"]);
      });

      // 数据处理完了
      // 不你没有
      // 开始处理 政策总数据
      this.citiesAndPoliciesData = {};
      this.policyProvinceCity = {};
      this.areaHotCityData = {};

      this.cityData.forEach(item => {
        // 处理 areaHotCityData

        if (!this.areaHotCityData[item["area"]]) {
          this.areaHotCityData[item["area"]] = [];
        }

        this.areaHotCityData[item["area"]].push(item["city"]);

        // 处理主要数据
        if (!this.citiesAndPoliciesData[item["city"]]) {
          this.citiesAndPoliciesData[item["city"]] = {};
        }

        this.policiesArray.forEach(policy => {
          // 处理 policiesAndAllCities
          let regEx = RegExp(`^${this.chartData.policy}\\d{4}$`);
          let isTheItem = Object.keys(item).some(j => {
            // console.log(j, this.chartData.policy);
            return regEx.test(j);
          });
          if (isTheItem) {
            if (!this.policiesAndAllCities[policy]) {
              this.policiesAndAllCities[policy] = [];
            }
            this.policiesAndAllCities[policy].push(item.city);
          }

          // 处理 policy -> province -> city 的数据

          if (!this.policyProvinceCity[policy]) {
            this.policyProvinceCity[policy] = {};
          }

          if (!this.policyProvinceCity[policy][item["province"]]) {
            this.policyProvinceCity[policy][item["province"]] = [];
          }

          this.policyProvinceCity[policy][item["province"]].push(item["city"]);

          if (!this.citiesAndPoliciesData[item["city"]][policy]) {
            this.citiesAndPoliciesData[item["city"]][policy] = [];
          }

          this.yearsArray.forEach(year => {
            this.citiesAndPoliciesData[item["city"]][policy].push({
              year,
              data: item[`${policy}${year}`] || 0
            });
          });
        });
      });
    },
    getPollutantData(type) {
      let tempArray = [];
      this.yearsArray.forEach(year => {
        // let itemOfTheYear = this.yearsAndDataArray.find(i => i.name === `${year}年`)
        // if (!itemOfTheYear) {
        // TODO: init this.yearsAndDataArray
        tempArray.push({
          name: `${year}年`,
          type: "bar",
          barWidth: 10,
          data: []
        });
        // }
      });
      this.yearsAndDataArray = tempArray;

      // TODO: 调整城市数量限制
      this.citiesArray = this.citiesArray.slice(0, this.limit);

      this.citiesArray.forEach(city => {
        console.log(city);
        if (!this.citiesAndPollutantsData[city]) {
          console.log(this.citiesAndPollutantsData);
        }
        if (!this.citiesAndPollutantsData[city][type]) {
          // 如果没有这个城市，直接每个 yearsAndDataArray push 一个 0
          this.yearsAndDataArray.forEach(i => {
            i.data.push(0);
          });

          return;
        }
        this.citiesAndPollutantsData[city][type].forEach(item => {
          // debugger
          this.yearsAndDataArray
            .find(i => i.name === `${item.year}年`)
            .data.push(item.data);
          // console.log(this.yearsAndDataArray);
          // let tempArray = this.yearsAndDataArray
          // let haha = tempArray.find(i => i.name === `${item.year}年`).data.push(item.data)

          // let theIndex = this.yearsAndDataArray.findIndex(i => i.name === `${item.year}年`)
          // if (theIndex > -1) {
          //   this.set(this.yearsAndDataArray, theIndex, [...this.yearsAndDataArray[theIndex], item.data])
          // }
        });
      });
    },
    chooseArea(type) {
      if (type === "我全都要") {
        if (this.chartData.tab === 1) {
          let pollutant = this.findPollutant(this.chartData.pollutant);
          this.citiesArray = this.pollutantsAndAllCities[pollutant];
        }
        if (this.chartData.tab === 0) {
          // debugger
          this.citiesArray = this.policiesAndAllCities[this.chartData.policy];
        }
        return;
      }
      if (type === "我很重要") {
        this.citiesArray = this.importantCity.map(i => i["城市"]);
        return;
      }

      if (this.chartData.tab === 1) {
        this.citiesArray = this.hotCityData[type];
      }
      if (this.chartData.tab === 0) {
        this.citiesArray = this.areaHotCityData[type];
      }
    },
    switchArea(number) {
      switch (number) {
        case 0:
          this.chooseArea("我全都要");
          break;
        case 1:
          this.chooseArea("我全都要");
          break;
        case 2:
          this.chooseArea("我很重要");
          break;
        case 3:
          this.chooseArea("京津冀");
          break;
        case 4:
          this.chooseArea("珠三角");
          break;
        case 5:
          this.chooseArea("长三角");
          break;
        default:
          break;
      }
    },
    findPollutant(number) {
      switch (number) {
        case 0:
          this.whoMax = 10;
          this.chinaMax = 35;
          return "PM2d5";
          break;
        case 1:
          this.whoMax = 40;
          this.chinaMax = 40;

          return "NO2";
          break;
        case 2:
          this.whoMax = 20;
          this.chinaMax = 70;
          return "PM10";
          break;
        case 3:
          this.whoMax = 100;
          this.chinaMax = 160;
          return "O3";
          break;
        case 4:
          this.whoMax = 0;
          this.chinaMax = 60;
          return "SO2";
          break;
        case 5:
          this.whoMax = 0;
          this.chinaMax = 4;
          return "CO";
          break;
        default:
          break;
      }
    },
    switchPollutant(number) {
      let pollutant = this.findPollutant(number);
      this.getPollutantData(pollutant);
    },
    switchPolicy(policy) {
      this.getPolicyData(policy);
    },
    getPolicyData(type) {
      let tempArray = [];
      this.yearsArray.forEach(year => {
        // let itemOfTheYear = this.yearsAndDataArray.find(i => i.name === `${year}年`)
        // if (!itemOfTheYear) {
        // TODO: init this.yearsAndDataArray
        tempArray.push({
          name: `${year}年`,
          type: "bar",
          barWidth: 10,
          data: []
        });
        // }
      });
      this.yearsAndDataArray = tempArray;

      // TODO: 调整城市数量限制
      this.citiesArray = this.citiesArray.slice(0, this.limit);

      this.citiesArray.forEach(city => {
        // console.log(city);
        if (!this.citiesAndPoliciesData[city][type]) {
          // 如果没有这个城市，直接每个 yearsAndDataArray push 一个 0
          this.yearsAndDataArray.forEach(i => {
            i.data.push(0);
          });

          return;
        }
        this.citiesAndPoliciesData[city][type].forEach(item => {
          // debugger
          this.yearsAndDataArray
            .find(i => i.name === `${item.year}年`)
            .data.push(item.data);
          // console.log(this.yearsAndDataArray);
          // let tempArray = this.yearsAndDataArray
          // let haha = tempArray.find(i => i.name === `${item.year}年`).data.push(item.data)

          // let theIndex = this.yearsAndDataArray.findIndex(i => i.name === `${item.year}年`)
          // if (theIndex > -1) {
          //   this.set(this.yearsAndDataArray, theIndex, [...this.yearsAndDataArray[theIndex], item.data])
          // }
        });
      });
    },
    filterYear(yearFilterArray) {
      this.echartsOption.series = this.yearsAndDataArray.filter(i =>
        yearFilterArray.some(year => `${year}年` === i.name)
      );
      // debugger
    },
    putChartOptions() {
      // TODO: 调整城市数量限制
      this.citiesArray = this.citiesArray.slice(0, this.limit);

      this.echartsOption.yAxis.data = this.citiesArray;

      this.echartsOption.series.unshift({
        type: "bar",
        markArea: {
          data: [
            [
              {
                name: `WHO标准(${this.whoMax}μg/m³)`,
                itemStyle: {
                  color: "#D5EEFF"
                },
                xAxis: 0,
                label: {
                  show: true
                }
              },
              {
                xAxis: this.whoMax
              }
            ],
            [
              {
                name: `中国国家标准(${this.chinaMax}μg/m³)`,
                itemStyle: {
                  color: "#95E2FE"
                },
                label: {
                  show: true,
                  offset:[40, 0]
                },
                xAxis: this.whoMax
              },
              {
                xAxis: this.chinaMax
              }
            ]
          ]
        },
        // itemStyle: {
        //   normal: {
        //     opacity: 0.8
        //   }
        // },
        // encode: {
        //   x: [1, 2],
        //   y: 0
        // },
        data: []
      });
      if (this.chinaMax===this.whoMax) {
          this.echartsOption.series[0].markArea.data[0][0].name='';
          this.echartsOption.series[0].markArea.data[1][0].name=`WHO/中国标准(${this.chinaMax}μg/m³)`
      }
      if (this.whoMax===0) {
         this.echartsOption.series[0].markArea.data[0][0].name='';
          this.echartsOption.series[0].markArea.data[1][0].name=`中国国家标准(${this.chinaMax}μg/m³)`
      }
      if (this.chartData.tab===0) {
        this.echartsOption.series[0].markArea = null
      }
      this.myChart.setOption(this.echartsOption, true); // https://ecomfe.github.io/echarts-doc/public/en/api.html#echartsInstance.setOption
    },
    refreshChart(newValue, oldValue) {
      if (newValue.area !== 0 && newValue.area !== 1) {
        // 如果不是 身份 城市 ，就清除 selectedList
        this.selectedList = [];
      }

      if (this.selectedList.length <= 0) {
        // 如果有筛选省或市，就不要走 switchArea
        // debugger
        // if (newValue.pollutant !== oldValue.pollutant) {
        this.switchArea(newValue.area);
      } else {
        // 待优化
        let pollutant = this.findPollutant(this.chartData.pollutant);
        if (newValue.area === 0) {
          this.citiesArray = this.selectedList.reduce((prev, next) => {
            return [...prev, ...this.pollutantProvinceCity[pollutant][next]];
          }, []);
        }
        if (newValue.area === 1) {
          this.citiesArray = this.selectedList;
        }

        // console.log(this.citiesArray);
      }
      this.switchPollutant(newValue.pollutant);

      let yearsArray = newValue.barchartYear.map(number => {
        switch (number) {
          case 0:
            return 2013;
            break;
          case 1:
            return 2014;
            break;
          case 2:
            return 2015;
            break;
          case 3:
            return 2016;
            break;
          case 4:
            return 2017;
            break;
          case 5:
            return 2018;
            break;

          default:
            break;
        }
      });

      // debugger
      this.filterYear(yearsArray);
      // }

      this.putChartOptions();
    },
    refreshPolicyChart(newValue, oldValue) {
      if (newValue.area !== 0 && newValue.area !== 1) {
        // 如果不是 身份 城市 ，就清除 selectedList
        this.selectedList = [];
      }

      if (this.selectedList.length <= 0) {
        // 如果有筛选省或市，就不要走 switchArea
        // debugger
        // if (newValue.pollutant !== oldValue.pollutant) {
        this.switchArea(newValue.area);
      } else {
        // 待优化

        // this.citiesArray = this.selectedList.reduce((prev, next) => {
        //   return [
        //     ...prev,
        //     ...this.policyProvinceCity[this.chartData.policy][next]
        //   ];
        // }, []);
        // console.log(this.citiesArray);

        if (newValue.area === 0) {
          this.citiesArray = this.selectedList.reduce((prev, next) => {
            return [
              ...prev,
              ...this.policyProvinceCity[this.chartData.policy][next]
            ];
          }, []);
        }
        if (newValue.area === 1) {
          this.citiesArray = this.selectedList;
        }
      }
      this.switchPolicy(newValue.policy);

      let yearsArray = newValue.barchartYear.map(number => {
        switch (number) {
          case 0:
            return 2013;
            break;
          case 1:
            return 2014;
            break;
          case 2:
            return 2015;
            break;
          case 3:
            return 2016;
            break;
          case 4:
            return 2017;
            break;
          case 5:
            return 2018;
            break;

          default:
            break;
        }
      });

      // debugger
      this.filterYear(yearsArray);
      // }

      this.putChartOptions();
    }
  }
};
</script>

